  // 下载文件
  downloadFile (file) {
    let auth = storage.get(STORAGE_KEY, {})
    var token = auth.access_token
    return axios({ // 用axios发送post请求
      method: 'post',
      url: `/web/commons/downloadFile?filename=${file}`, // 请求地址
      headers: {'access_token': token},
      responseType: 'blob' // 表明返回服务器返回的数据类型
    })
  },
